<template>
  <div :class="$route.name">
    <div class="wrap-basic" :class="state.active">
      <div class="tit">
        <span class="day">000일차</span>
        <span class="han">ㅏ</span>
      </div>
      <ul class="contents">
        <li>
          <router-link :to="{ name: 'activity', params: { name: 'today' } }">
            오늘 한글
          </router-link>
        </li>
        <li>
          <router-link :to="{ name: 'activity', params: { name: 'rhythm' } }">
            리듬 한글
          </router-link>
        </li>
        <li>
          <router-link :to="{ name: 'activity', params: { name: 'paint' } }">
            색칠 한글
          </router-link>
        </li>
        <li>
          <router-link :to="{ name: 'activity', params: { name: 'partner' } }">
            짝꿍 한글
          </router-link>
        </li>
        <li>
          <router-link :to="{ name: 'activity', params: { name: 'myHangul' } }">
            나의 한글
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    state: {
      type: Object,
      default: null
    }
  }
}
</script>

<style lang="scss" scoped>
.wrap-basic {
  overflow: hidden;
  padding-top: 37px;
  .tit {
    float: left;
    position: relative;
    width: 308px;
    height: 110px;
    background: $colorF;
    border-radius: 0 20px 20px 20px;
    text-align: center;
    line-height: 100px;
    .day {
      position: absolute;
      top: -37px;
      left: -3px;
      width: 100px;
      height: 37px;
      border-radius: 17px 17px 0 0;
      font-family: $mainFont;
      line-height: 34px;
      letter-spacing: 1px;
      font-size: 20px;
      color: $colorF;
    }
    .han {
      font-size: 70px;
    }
  }
  .contents {
    float: left;
    padding-left: 72px;
    background-repeat: no-repeat;
    background-position: 9px center;

    li {
      position: relative;
      float: left;
      width: 160px;
      height: 110px;
      border-radius: 20px;
      padding-top: 37px;
      font-size: 30px;
      text-align: center;
      cursor: pointer;
      a {
        display: block;
        position: absolute;
        top: 0;
        line-height: 110px;
        width: 100%;
        height: 100%;
      }
      & + li {
        margin-left: 10px;
      }
      &::before {
        position: absolute;
        top: -24px;
        left: 50px;
        content: '';
        display: block;
        width: 50px;
        height: 50px;
        background: url('~@/assets/images/common/ico_play.svg') no-repeat 14px
          center;
        background-size: 26px 24px;
        border-radius: 100%;
      }
    }
  }
}
@mixin stepColor($defualt, $active) {
  .tit {
    border: 3px solid $defualt;
    .day {
      background: $defualt;
    }
    .han {
      color: $defualt;
    }
  }
  .contents {
    li {
      border: 3px solid $defualt;
      color: $defualt;
      &::before {
        background-color: $defualt;
      }
    }
  }
  .active {
    .tit {
      border: 3px solid $active;
      .day {
        background: $active;
      }
      .han {
        color: $active;
      }
    }
    .contents {
      li {
        background: $colorF;
        border: 3px solid $active;
        color: $active;
        &::before {
          background-color: $active;
        }
      }
    }
  }
  .disable {
    .tit {
      background: transparent;
      border: 3px solid $colorB;
      .day {
        background: $colorB;
      }
      .han {
        color: $colorB;
      }
    }
    .contents {
      background-image: url('~@/assets/images/main/oneday/ico_dot_disable.svg');
      li {
        border: 3px solid $colorB;
        color: $colorB;
        &::before {
          background-color: $colorB;
        }
      }
    }
  }
}
.onedayStep01 {
  @include stepColor($step01Color, $step01ColorActive);
  .contents {
    background-image: url('~@/assets/images/main/oneday/ico_dot01_defualt.svg');
  }
}
.onedayStep02 {
  @include stepColor($step02Color, $step02ColorActive);
  .contents {
    background-image: url('~@/assets/images/main/oneday/ico_dot02_defualt.svg');
  }
}
.onedayStep03 {
  @include stepColor($step03Color, $step03ColorActive);
  .contents {
    background-image: url('~@/assets/images/main/oneday/ico_dot03_defualt.svg');
  }
}
.onedayStep04 {
  @include stepColor($step04Color, $step04ColorActive);
  .contents {
    background-image: url('~@/assets/images/main/oneday/ico_dot04_defualt.svg');
  }
}
.onedayStep05 {
  @include stepColor($step05Color, $step05ColorActive);
  .contents {
    background-image: url('~@/assets/images/main/oneday/ico_dot05_defualt.svg');
  }
}
.onedayStep06 {
  @include stepColor($step06Color, $step06ColorActive);
  .contents {
    background-image: url('~@/assets/images/main/oneday/ico_dot06_defualt.svg');
  }
}
</style>
